"use client"

import React, { useRef, useEffect, useState, useCallback } from "react"
import { useColorTheme } from "./color-context"
import { hslToHex, formatHsl } from "@/lib/color-utils"

export function ColorWheel() {
  const { theme, updatePrimaryColor } = useColorTheme()
  const wheelRef = useRef<HTMLDivElement>(null)
  const containerRef = useRef<HTMLDivElement>(null)
  const [isDragging, setIsDragging] = useState(false)
  const [center, setCenter] = useState({ x: 0, y: 0 })
  const [isFocused, setIsFocused] = useState(false)

  const updateMetrics = useCallback(() => {
    if (wheelRef.current) {
      const rect = wheelRef.current.getBoundingClientRect()
      setCenter({
        x: rect.left + rect.width / 2,
        y: rect.top + rect.height / 2,
      })
    }
  }, [])

  useEffect(() => {
    updateMetrics()
    window.addEventListener("resize", updateMetrics)
    return () => window.removeEventListener("resize", updateMetrics)
  }, [updateMetrics])

  const calculateAngle = useCallback(
    (clientX: number, clientY: number) => {
      const dx = clientX - center.x
      const dy = clientY - center.y
      let angle = Math.atan2(dy, dx) * (180 / Math.PI)
      angle = angle + 90

      if (angle < 0) {
        angle += 360
      }

      updatePrimaryColor(Math.round(angle) % 360)
    },
    [center, updatePrimaryColor]
  )

  const handleStart = useCallback(
    (e: React.MouseEvent | React.TouchEvent) => {
      setIsDragging(true)
      updateMetrics()
      
      let clientX: number
      let clientY: number

      if ("touches" in e) {
        clientX = e.touches[0].clientX
        clientY = e.touches[0].clientY
      } else {
        clientX = e.clientX
        clientY = e.clientY
      }

      calculateAngle(clientX, clientY)
      e.preventDefault()
    },
    [updateMetrics, calculateAngle]
  )

  const handleMove = useCallback(
    (e: MouseEvent | TouchEvent) => {
      if (!isDragging) return

      let clientX: number
      let clientY: number

      if ("touches" in e) {
        clientX = e.touches[0].clientX
        clientY = e.touches[0].clientY
      } else {
        clientX = e.clientX
        clientY = e.clientY
      }

      calculateAngle(clientX, clientY)
    },
    [isDragging, calculateAngle]
  )

  const handleEnd = useCallback(() => {
    setIsDragging(false)
  }, [])

  // Keyboard navigation
  const handleKeyDown = useCallback(
    (e: React.KeyboardEvent) => {
      if (!isFocused) return

      let newHue = theme.primary.h
      const step = e.shiftKey ? 10 : 1 // Shift for larger steps

      switch (e.key) {
        case "ArrowUp":
          e.preventDefault()
          newHue = (newHue - step + 360) % 360
          break
        case "ArrowDown":
          e.preventDefault()
          newHue = (newHue + step) % 360
          break
        case "ArrowLeft":
          e.preventDefault()
          newHue = (newHue - step + 360) % 360
          break
        case "ArrowRight":
          e.preventDefault()
          newHue = (newHue + step) % 360
          break
        case "Home":
          e.preventDefault()
          newHue = 0
          break
        case "End":
          e.preventDefault()
          newHue = 359 // End of the color spectrum (0-359)
          break
        default:
          return
      }

      updatePrimaryColor(newHue)
    },
    [isFocused, theme.primary.h, updatePrimaryColor]
  )

  useEffect(() => {
    if (isDragging) {
      window.addEventListener("mousemove", handleMove)
      window.addEventListener("touchmove", handleMove)
      window.addEventListener("mouseup", handleEnd)
      window.addEventListener("touchend", handleEnd)

      return () => {
        window.removeEventListener("mousemove", handleMove)
        window.removeEventListener("touchmove", handleMove)
        window.removeEventListener("mouseup", handleEnd)
        window.removeEventListener("touchend", handleEnd)
      }
    }
  }, [isDragging, handleMove, handleEnd])

  const primaryHex = hslToHex(theme.primary.h, theme.primary.s, theme.primary.l)
  const compHex = hslToHex(
    theme.complementary.h,
    theme.complementary.s,
    theme.complementary.l
  )

  return (
    <div className="flex flex-col gap-6">
      <div className="flex justify-center">
        <div className="relative w-[300px] h-[300px]" ref={wheelRef}>
          <div
            ref={containerRef}
            role="slider"
            aria-label="Primary color picker"
            aria-valuemin={0}
            aria-valuemax={360}
            aria-valuenow={theme.primary.h}
            aria-valuetext={`Hue: ${theme.primary.h} degrees`}
            tabIndex={0}
            className={`w-full h-full rounded-full cursor-grab active:cursor-grabbing touch-none transition-all ${
              isFocused ? "ring-4 ring-[var(--color-primary)] ring-offset-2 ring-offset-slate-900" : ""
            }`}
            style={{
              background: `conic-gradient(
                hsl(0, 100%, 50%),
                hsl(60, 100%, 50%),
                hsl(120, 100%, 50%),
                hsl(180, 100%, 50%),
                hsl(240, 100%, 50%),
                hsl(300, 100%, 50%),
                hsl(360, 100%, 50%)
              )`,
              boxShadow: "0 0 50px rgba(0,0,0,0.5), 0 0 0 10px rgba(255, 255, 255, 0.05)",
            }}
            onMouseDown={handleStart}
            onTouchStart={handleStart}
            onKeyDown={handleKeyDown}
            onFocus={() => setIsFocused(true)}
            onBlur={() => setIsFocused(false)}
          >
            {/* Primary selector */}
            <div
              className="absolute top-1/2 left-1/2 w-1 origin-top transition-transform duration-100"
              style={{
                height: "50%",
                transform: `translate(-50%, -50%) rotate(${theme.primary.h}deg)`,
              }}
              aria-hidden="true"
            >
              <div
                className="absolute bottom-[-15px] left-1/2 -translate-x-1/2 w-6 h-6 rounded-full border-3 border-white shadow-lg"
                style={{
                  backgroundColor: primaryHex,
                  boxShadow: `0 0 15px ${primaryHex}`,
                }}
              />
            </div>

            {/* Complementary selector */}
            <div
              className="absolute top-1/2 left-1/2 w-1 origin-top opacity-60 transition-transform duration-100"
              style={{
                height: "50%",
                transform: `translate(-50%, -50%) rotate(${theme.complementary.h}deg)`,
              }}
              aria-hidden="true"
            >
              <div
                className="absolute bottom-[-10px] left-1/2 -translate-x-1/2 w-4 h-4 rounded-full border-2 border-white/80"
                style={{
                  backgroundColor: compHex,
                }}
              />
            </div>

            {/* Center cap */}
            <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-[30%] h-[30%] bg-[#0f172a] rounded-full shadow-inner z-10 flex items-center justify-center">
              <div
                className="w-[80%] h-[80%] rounded-full opacity-80 blur-[10px]"
                style={{
                  background: `linear-gradient(135deg, ${primaryHex}, ${compHex})`,
                }}
              />
            </div>
          </div>
        </div>
      </div>

      {/* Color info cards */}
      <div className="flex gap-4">
        <ColorCard
          label="PRIMARY"
          hex={primaryHex}
          hsl={formatHsl(theme.primary.h, theme.primary.s, theme.primary.l)}
          color={primaryHex}
        />
        <ColorCard
          label="COMPLEMENTARY"
          hex={compHex}
          hsl={formatHsl(
            theme.complementary.h,
            theme.complementary.s,
            theme.complementary.l
          )}
          color={compHex}
        />
      </div>

      {/* Keyboard instructions */}
      <div className="text-xs text-slate-400 text-center mt-2">
        <p>Use arrow keys to adjust hue (Shift for larger steps)</p>
      </div>
    </div>
  )
}

function ColorCard({
  label,
  hex,
  hsl,
  color,
}: {
  label: string
  hex: string
  hsl: string
  color: string
}) {
  return (
    <div
      className="flex-1 bg-slate-800/70 backdrop-blur-sm border border-white/10 rounded-2xl p-4 flex flex-col items-center gap-3 transition-all hover:border-white/20 hover:-translate-y-0.5"
      role="region"
      aria-label={`${label} color information`}
    >
      <div
        className="w-full h-16 rounded-xl shadow-inner"
        style={{ backgroundColor: color }}
        aria-hidden="true"
      />
      <div className="w-full text-left">
        <span className="block text-xs uppercase tracking-wider text-slate-400 mb-2 text-center">
          {label}
        </span>
        <div className="flex justify-between text-sm py-1 border-b border-white/5">
          <span className="text-slate-500">HEX</span>
          <span className="font-mono text-white" aria-label={`Hex value: ${hex}`}>
            {hex}
          </span>
        </div>
        <div className="flex justify-between text-sm py-1">
          <span className="text-slate-500">HSL</span>
          <span className="font-mono text-white" aria-label={`HSL value: ${hsl}`}>
            {hsl}
          </span>
        </div>
      </div>
    </div>
  )
}
